/*colors*/
.logo {
  display: flex;
  flex-direction: row;
  flex-grow: 1; }

.logo-header__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 29px;
  margin: 0; }

.logo-header__paragraph {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  margin: 0; }

.logo-header {
  display: flex;
  flex-basis: 50%;
  padding-left: 20px; }

.info {
  padding-left: 14px;
  padding-right: 14px;
  margin: 0 auto;
  margin-top: 180px;
  display: flex;
  flex-direction: row;
  position: relative; }
  @media (min-width: 660px) {
    .info {
      padding-left: 25px;
      padding-right: 25px; } }
  @media (min-width: 960px) {
    .info {
      padding-right: 235px;
      padding-left: 235px; } }

.info__general {
  padding: 41px 0 10px 15px;
  max-width: 720px; }

.info::before {
  position: absolute;
  content: "";
  background-image: url("../img/chip.png");
  z-index: 10;
  width: 705px;
  height: 515px;
  background-repeat: no-repeat;
  top: -16px;
  right: 161px; }

.info__header {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  text-transform: uppercase;
  white-space: nowrap;
  margin: 0;
  letter-spacing: 0.07em; }

.info__paragraph {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px;
  margin: 0; }

.info__inner {
  position: relative;
  padding: 28px 0 0 97px; }

.info__inner::before {
  position: absolute;
  content: "";
  background-color: #ffffff;
  height: 2px;
  width: 66px;
  left: 5px;
  top: 45px; }

.info__picture {
  max-width: 705px; }

.button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  background: #00b2ff;
  border-radius: 5px;
  color: #ffffff;
  border: none; }

.button--contacts {
  background-color: transparent;
  padding: 14px 28px;
  border: 1px solid #ffffff;
  letter-spacing: 0.7px; }

.button--info {
  border-radius: 5px;
  padding: 18px 45px;
  margin-top: 43px;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.8px; }

.header {
  position: relative; }

.header__wrapper {
  background: #1b2d37;
  background-image: url("../img/image.png");
  background-size: cover;
  color: #ffffff;
  padding-top: 34px; }
  @media (min-width: 960px) {
    .header__wrapper {
      min-height: 880px; } }

.header__inner {
  padding-left: 14px;
  padding-right: 14px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }
  @media (min-width: 660px) {
    .header__inner {
      padding-left: 25px;
      padding-right: 25px; } }
  @media (min-width: 960px) {
    .header__inner {
      padding-right: 235px;
      padding-left: 235px; } }

.contacts {
  flex-grow: 1;
  font-size: 16px;
  line-height: 18px; }

.contacts__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 3px 0; }

.contacts__item:nth-child(1),
.contacts__item:nth-child(2) {
  padding-left: 40px;
  text-align: center; }

.contacts__link {
  color: #ffffff;
  text-decoration: none; }

.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-left: 14px;
  padding-right: 14px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding-left: 100px;
  padding-right: 100px;
  margin: 0 auto; }
  @media (min-width: 660px) {
    .advantages__list {
      padding-left: 25px;
      padding-right: 25px; } }
  @media (min-width: 960px) {
    .advantages__list {
      padding-right: 235px;
      padding-left: 235px; } }

.advantages__item {
  min-height: 300px;
  flex: 1 0 50%;
  border: 1px solid #ebebeb; }
