.logo {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
}

.logo-header__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 29px;
  margin: 0;
}

.logo-header__paragraph {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  margin: 0;
}

.logo-header {
  display: flex;
  flex-basis: 50%;
  padding-left: 20px;
}
